<template>
  <div class="form-login px-5">
    <div class="form-head">
      <h1>Login</h1>
      <hr />
    </div>
    <div class="form-body mt-5">
      <form>
        <div class="form-group">
          <div class="mt-3">
            <label class="text-left d-block" for="form-email">Email address</label>
            <input
              type="email"
              v-model="form.email"
              class="form-control"
              id="form-email"
              aria-describedby="emailHelp"
            />
          </div>
          <div class="mt-3">
            <label class="text-left d-block" for="form-password">Password</label>
            <input
              type="password"
              v-model="form.password"
              class="form-control"
              id="form-password"
              aria-describedby="emailHelp"
            />
          </div>
        </div>
        <h5 class="error-text" v-if="error">{{ error }}</h5>
        <p>
          Belum punya akun ?
          <router-link to="/auth/register">
            <strong class="link">Daftar</strong>
          </router-link>
        </p>
        <button
          type="submit"
          @click="submitClick"
          :disabled="isError"
          class="btn btn-primary mt-2"
        >Submit</button>
      </form>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import { addVisitor } from "@/utils/index";

export default {
  name: "login",
  data() {
    return {
      status: {
        error: null,
        success: null,
      },
      show: true,
      form: {
        email: "",
        password: "",
      },
      error: null,
    };
  },
  mounted() {
    if (this.$route.params.error) {
      this.error = this.$route.params.error;
    }
  },
  inject:['loading'],
  methods: {
    ...mapActions(["userAuth"]),
    async submitClick(e) {
      e.preventDefault();
      try {
        this.loading.value = true;
        await this.userAuth(this.form);
        await addVisitor();
        setTimeout(() => {
          this.loading.value = false;
          this.$router.push({ name: "home" });
        },1000)
      } catch (error) {
        this.error = error.toString().split(":")[3];
        this.loading.value = false;
      }
    },
    toggle() {
      this.show = !this.show;
    },
  },
  computed: {
    isError() {
      if (this.form.password.length <= 6) {
        return true;
      } else {
        return false;
      }
    },
  },
};
</script>

